body {
  margin: 0;
  display: flex;
  font-family: sans-serif;
  width: 21cm;
  height: 29.7cm;
  background-color: rgb(211, 105, 105);
  padding: 50px;
}

.projects-wrapper {
  display: flex;
  height: 10cm;
  width: 21cm;
}

.gallery-wrapper {
  width: 21cm;
  height: 5cm;
  -webkit-box-shadow: -1px 9px 15px -5px rgba(17, 17, 17, 1);
  -moz-box-shadow: -1px 9px 15px -5px rgba(17, 17, 17, 1);
  box-shadow: -1px 9px 15px -5px rgba(17, 17, 17, 1);
  overflow: hidden;
  position: relative;
}

.description {
  flex: 0 0 25%;
  padding-right: 30px;
}

.description h1 {
  letter-spacing: 1.5px;
  margin: 0;
}

.description p {
  font-size: 17px;
  width: 5cm;
}

.gallery {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  gap: 10px;
}

.gallery-div {
  flex: 0 0 33.3333333%;
  position: relative;
}

.responsive-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
